body {
    font-family: var(--main-font);
    color: var(--main-txt-cl);
    font-size: 18px;
    line-height: calc(30 / 18);
    letter-spacing: 0.02em;
  }
  
  // .section {
  //   padding-top: 120px;
  //   padding-bottom: 120px;
  // }
  
  .main-title {
    margin-bottom: 20px;
    width: 450px;
    font-family: var(--secondary-font);
    font-size: 72px;
    line-height: calc(85 / 72);
    letter-spacing: 0.05em;
    color: transparent;
  
    -webkit-background-clip: text;
    background-clip: text;
  
    animation: slide-right 2000ms ease-in 200ms both;
  }
  
  @keyframes slide-right {
    0% {
      opacity: 0;
      transform: translateX(350px) scale(0.7);
      transform-origin: left top;
      background-image: linear-gradient(#ff6c00, #ff6c00);
      background-repeat: no-repeat;
      background-position: left -650px center;
      -webkit-text-stroke: 2px var(--dark-txt-cl);
    }
  
    25% {
      opacity: 1;
    }
  
    50% {
      transform: translateX(0) scale(0.7);
      transform-origin: center;
      -webkit-text-stroke: 2px var(--dark-txt-cl);
    }
  
    100% {
      transform: scale(1);
      background-image: linear-gradient(#ff6c00, #ff6c00);
      background-repeat: no-repeat;
      background-position: left 0 center;
      -webkit-text-stroke: 2px var(--accent-txt-cl);
    }
  }
  
